/*jshint esversion: 6 */
import Vue from 'vue'
import Vuex from 'vuex'
import _ from "lodash"
import enums from "./enums.js"

Vue.use(Vuex)

const newPerson = () => ({
  renaming: true,
  name: "",
  status: enums.status.COMING
})

export default new Vuex.Store({
  state: {
    people: [newPerson()],
    minutesToWaitFor: 8,
    timerOn: false,
    timerPlayed: false,
    remainingTimeDisplay: "",
  },
  getters: {
    titleText({timerOn, timerPlayed}) {
      if (!timerOn && !timerPlayed) {
        return "when everyone is <span class='statusHere'>HERE</span>, <br/>we can <span class='statusWaiting'>WAIT</span> together"
      } else if (timerOn) {
        return "when everyone has <span class='statusWaiting'>WAITED</span>, <br/>we will be <span class='statusReady'>READY</span>"
      } else if (timerPlayed) {
        return "when everyone is <span class='statusReady'>READY</span>, <br/>we can <span class='statusNotComing'>move on</span>"
      } else {
        ""
      }
    },
    noOneIsComing({
      people
    }) {
      return _.every(people, person => person.status === enums.status.NOT_COMING)
    },
    someoneIsRenaming({
      people
    }) {
      return _.some(people, "renaming")
    },
    someoneIsComing(state) {
      return _.some(state.people, person => person.status === enums.status.COMING)
    },
    canStartWaiting(state, getters) {
      return !_.some([
        getters.someoneIsComing,
        getters.someoneIsRenaming,
        getters.noOneIsComing,
        state.timerOn,
        state.timerPlayed
      ])
    },
    shouldShowAddButton(state) {
      if (state.people.length == 1 && state.people[0].renaming) {
        return false
      } else if (state.timerOn || state.timerPlayed) {
        return false
      } else return true
    },
  },
  mutations: {
    enableRenamingMode(state, idx) {
      state.people[idx].renaming = true
    },
    disableRenamingMode(state, idx) {
      state.people[idx].renaming = false
    },
    renamePerson(state, {
      idx,
      newName
    }) {
      state.people[idx].renaming = false
      state.people[idx].name = newName
    },
    changePersonStatus(state, {
      idx,
      newStatus
    }) {
      state.people[idx].status = newStatus
    },
    addNewPerson(state) {
      state
        .people
        .push(newPerson())
    },
    setAllPeopleToWaiting(
      {people}
    ) {
      people
        .forEach(person => {
          person.status = enums.status.WAITING
        })
    },
    setAllPeopleToReady({people}) {
      people
        .forEach(person => {
          person.status = enums.status.READY
        })
    },
    updateWaitMinutes(
      state,
      newMinutes
    ) {
      state.minutesToWaitFor = newMinutes
    },
    changeRemainingTimeDisplay(
      state,
      newStr
    ) {
      state.remainingTimeDisplay = newStr
    },
    turnOnTimer(state) {
      state.timerOn = true
    },
    turnOffTimer(state) {
      state.timerOn = false
      state.timerPlayed = true
    },
  },
  actions: {
    [enums.actions.PERSON_RENAME_START]: ({
      state,
      commit
    }, idx) => {
      if (!(state.timerOn || state.timerPlayed)) {
        commit("enableRenamingMode", idx)
      }
    },
    [enums.actions.PERSON_RENAME_CANCEL]: ({
      commit
    },
      idx
  ) => {
      commit("disableRenamingMode", idx)
    },
    [enums.actions.PERSON_RENAME_CONFIRM]: ({
      commit
    }, {idx, newName}) => {
      if (newName.length > 0) {
        commit("renamePerson", {idx, newName})
      }

    },
    [enums.actions.PERSON_STATUS_CYCLE]: ({
        state,
        commit
      },
      idx
    ) => {
      if (state.timerPlayed) {
        commit("changePersonStatus", {
          idx,
          newStatus: enums.status.READY
        })
      } else if (!state.timerOn) {
        let statusCycle = [enums.status.COMING, enums.status.NOT_COMING, enums.status.HERE]
        let newStatus = statusCycle[(statusCycle.indexOf(state.people[idx].status) + 1) % 3]

        commit("changePersonStatus", {
          idx,
          newStatus
        })

      }
    },
    [enums.actions.ADD_PERSON]: ({
      commit
    }) => {
      commit("addNewPerson")
    },
    [enums.actions.START_WAITING]: ({
      state,
      commit
    }) => {
      commit("setAllPeopleToWaiting")

      let addMinutes = (date, minutes) => {
        return new Date(date.getTime() + minutes*60000);
      }

      commit("turnOnTimer")

      let countDownTo = addMinutes(new Date(), state.minutesToWaitFor).getTime()
      let interval = setInterval(
        () => {
          let now = new Date().getTime()
          let dist = countDownTo - now

          let remainingTime = [
            ["days", Math.floor(dist / (1000 * 60 * 60 * 24))],
            ["hours", Math.floor((dist % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60))],
            ["minutes", Math.floor((dist % (1000 * 60 * 60)) / (1000 * 60))],
            ["seconds", Math.floor((dist % (1000 * 60)) / 1000)],
          ]

          commit(
            "changeRemainingTimeDisplay",
            remainingTime
              .map(([unit, amt]) =>
                amt > 0
                  ? [amt, unit].join(" ")
                  : ""
              )
              .join(" ")
          )
          if (_.every(remainingTime, measure => measure[1] == 0)) {
            clearInterval(interval)
            commit("turnOffTimer")
            commit("setAllPeopleToReady")
          }
        },
        500
      )
    },
    [enums.actions.UPDATE_MINUTES]: ({
      commit
    }, {target: {value}}) => {
      commit("updateWaitMinutes", parseInt(value))
    }
  }
})