<template>
  <div>

    <span>
      <button
      class="waitButtonButton" @click="startWaiting">WAIT</button>
    </span>

    <span> for </span>

    <span>
      <input
        class="minuteInput"
        type="number"
        :value="minutesToWaitFor"
        @input="updateMinutesToWaitFor"
        min="0"
      >
    </span>

    <span> minutes</span>


  </div>
</template>


<script>
import { mapActions, mapState } from 'vuex'
import enums from "../enums.js"
export default {
  name: "WaitButton",
  computed: {
    ...mapState(["minutesToWaitFor"])
  },
  methods: {
    ...mapActions({
      startWaiting: enums.actions.START_WAITING,
      updateMinutesToWaitFor: enums.actions.UPDATE_MINUTES
    })
  }
}
</script>

