<template>
  <div class="Person">
    <span>
      <span v-if="renaming">

        <input
          :value="name"
          @input="updateNameInternally"
          v-on:keyup.enter="onRenameSubmit({idx, newName: textInput})"
          size="8"
          autofocus
          >
        <button class="confirmDeny" @click="onRenameSubmit({idx, newName: textInput})">✅</button>
        <button class="confirmDeny" @click="onRenameCancel(idx)">❌</button>

      </span>
      <span
        class="hoverable"
        v-else @click="personRenameStart(idx)">{{name}}</span>
    </span>
    <span> is </span>
    <span
      class="hoverable"
      v-bind:class="cssClasses()"
      @click="onStatusClick(idx)">{{status.replace("_", " ")}}</span>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import enums from "../enums.js"

export default {
  name: "Person",
  props: ["idx", "renaming", "name", "status"],

  data() {
    return {
      textInput: this.$props.name,
      cssClasses() {
        return {
          statusComing: this.status === enums.status.COMING,
          statusNotComing: this.status === enums.status.NOT_COMING,
          statusHere: this.status === enums.status.HERE,
          statusWaiting: this.status === enums.status.WAITING,
          statusReady: this.status === enums.status.READY
        }
      }
    }
  },
  methods: {
    updateNameInternally(e) {
      this.textInput = e.target.value
    },
    onRenameCancel(idx) {
      if (this.textInput.length > 0 ) {
        this.textInput = this.$props.name
        this.$store.dispatch(
          enums.actions.PERSON_RENAME_CANCEL,
          idx
        )
      }
    },
    ...mapActions({
      personRenameStart: enums.actions.PERSON_RENAME_START,
      onStatusClick: enums.actions.PERSON_STATUS_CYCLE,
      onRenameSubmit: enums.actions.PERSON_RENAME_CONFIRM
    })
  }
}
</script>

<style>

.hoverable:hover {
  border-bottom: dotted 3px;
  cursor: pointer;
}

button:hover {
  opacity: 0.5;
}

input {
  background-color: transparent;
  border: 0;
  border-bottom: dotted 3px #000;
  outline: none;
}

.Person {
  margin-bottom: 6px;
}

.confirmDeny {
  margin-left: 8px;
  display: inline-block;
}
</style>